.container {
	width: 100%;
	height: 100%;
	position: relative;
	background-color: white;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
}

.container svg {
	display: block;
	width: 100%;
	height: 100%;
}

.male {
	fill: rgb(22, 130, 218);
}

.female {
	fill: rgb(246, 0, 0);
}

.node text {
	font: 12px sans-serif;
}

.labels > * {
	pointer-events: none;
	background-color: brown;
}

svg .labels, svg text {
	pointer-events: none;
}

div.tooltip {
	position: absolute;
	text-align: center;
	width: 110px;
	padding: 10px;
	font: 12px sans-serif;
	background: lightsteelblue;
	border: 0;
	border-radius: 8px;
	pointer-events: none;
}